@charset "UTF-8";
/* main.css */

/* 메인 영역 */
main > h2 {
  text-align: center;
  font-size: 20px;
}

/* 상품 입력 */
.dataform {
  width: 100%;
  max-width: 1200px;
  margin: 10px auto;
}
.dataform > fieldset {
  border: none;
  width: 50%;
  margin: 0 auto;
}
.dataform p {
  margin: 6px 0;
  font-size: 16px;
}
.dataform p > label {
  width: 24%;
  display: inline-block;
  text-align: left;
}
.dataform p > input {
  width: 75%;
  padding: 8px 0;
  text-indent: 4px;
}
.dataform input[type=button] {
  width: 100%;
}

.dataform input[type=button], .btn_wrap > input {
  border: none;
  padding: 8px 15px;
  background: #ddd;
  border-radius: 3px;
  cursor: pointer;
}
.dataform input[type=button]:hover, .btn_wrap > input:hover {
  background-color: #4e4e4e;
  color: #fff;
}

/* 상품 출력 */
.booklist {
  display: flex;
  flex-wrap: wrap;
  margin: 15px auto;
}
.booklist > li {
  width: 47.5%;
  margin: 10px;
}
.booklist > li .img_wrap {
  width: 100%;
  max-height: 600px;
  overflow: hidden;
  border: 1px solid #f3f3f3;
}
.booklist > li img {
  width: 100%;
  max-height: 645px;
  object-fit: contain;
  transition: 0.3s;
}
.booklist > li img:hover {
  transform: scale(1.05);
  filter: contrast(1.2);
}
.booklist > li .bookname {
  font-weight: bold;
  padding: 2px 0;
}
.booklist > li .bookprice {
  padding: 3px 0;
}
.booklist > li .btn_wrap {
  padding: 5px 0;
}